.topLine {
  width: 76%;
  min-width: 310px;
  height: fit-content;
  display: flex;
  justify-content: right;
  padding-right: 5%;
  .toggleSwitch {
    font-size: 0.8rem;
    height: 10px;
    margin-right: 1%;
  }
  .langSelect {
    border-radius: 5px;
    font-size: 0.8rem;
    padding: 2px;
    background-color: rgba(255, 255, 255, 0);
  }
}

.rightLine {
  border-left: 1px solid gray;
  position: fixed;
  top: 50%;
  right: 5%;
  transform: translateY(-50%);
  height: 45%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  .ul-wrapper {
    font-size: 1.1rem;
    color: gray;
    display: grid;
    gap: 20px;
    text-align: left;
    .li-wrapper {
      margin-left: -24px;
      cursor: pointer;
    }
  }
}

.main-test {
  width: 100vw;
  height: fit-content;
  /* background-color: rgb(44, 44, 44); */
  text-align: center;
  font-size: 2.2rem;
  white-space: pre-line;
  /* padding: 0 20%; */
  /* color: white; */
}

.h-line {
  width: 50%;
  min-width: 300px;
  border-bottom: solid 1px gray;
  margin: auto;
  margin-top: 1%;
  margin-bottom: 3%;
}

.profile-info {
  width: 100%;
  max-width: 100%;
  height: fit-content;
  margin-top: 2%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  padding: 2%;
  .my-image {
    max-height: 188px;
    max-width: 150px;
    min-height: 125px;
    min-width: 100px;
    width: 7vw;
    margin-top: auto;
    margin-bottom: auto;
  }
  .description {
    width: 40vw;
    min-width: 300px;
    padding: 1%;
    padding-left: 4%;
    text-align: left;
    .line {
      font-size: 1.2rem;
      display: flex;
      align-items: center;
      margin-top: 2%;
      .icon-image {
        margin-right: 3%;
        width: 25px;
        height: 25px;
        max-width: 25px;
        max-height: 25px;
      }
    }
    .my-name {
      /* color: rgb(96, 174, 248); */
      font-weight: bold;
    }
  }
}

.introduce {
  width: 100%;
  height: fit-content;

  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  .header {
    width: 8vw;
    min-width: 230px;
    font-size: 1.7rem;
    /* color: rgb(96, 174, 248); */
    font-weight: bold;
    text-align: start;
  }
  .content {
    width: 35vw;
    min-width: 300px;
    font-size: 0.9rem;
    word-break: keep-all;
    text-align: start;
  }
}

.skill {
  width: 100%;
  height: fit-content;

  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  .header {
    width: 8vw;
    min-width: 230px;
    font-size: 1.7rem;
    /* color: rgb(96, 174, 248); */
    font-weight: bold;
    text-align: left;
  }
  .table {
    margin-top: 1%;
    font-size: 1rem;
    text-align: center;
    width: 35vw;
    min-width: 300px;
    .td {
      border: solid 1px gray;
      padding: 2%;
    }
  }
}

table, th, td {
  border-collapse : collapse;
}

.work-exp {
  width: 100%;
  height: fit-content;

  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  .header {
    width: 47%;
    min-width: 230px;
    font-size: 1.7rem;
    /* color: rgb(96, 174, 248); */
    font-weight: bold;
    text-align: left;
  }
  .exp {
    width: 100%;
    font-size: 1rem;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 1%;

    .period {
      width: 8vw;
      min-width: 230px;
      color: gray;
      font-size: 1.3rem;
      font-style: italic;
      .box {
        font-style: normal;
        border-radius: 5px;
        background-color: rgb(94, 200, 207);
        color: black;
        width: fit-content;
        font-size: 1rem;
        margin-top: 3%;
        margin-left: 50%;
        padding-left: 3%;
        padding-right: 3%;
        /* box-shadow: 1px 1px 1px 1px gray; */
      }
    }
    .content {
      width: 35vw;
      min-width: 300px;
      text-align: left;
      .company-name {
        font-size: 1.4rem;
        font-weight: bold;
      }
      .company-role {
        font-style: italic;
        color: gray;
        margin-bottom: 3%;
      }
      li {
        margin-top: 1%;
      }
    }
  }
}

.project {
  width: 100%;
  height: fit-content;

  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  .header {
    width: 47%;
    min-width: 230px;
    font-size: 1.7rem;
    /* color: rgb(96, 174, 248); */
    font-weight: bold;
    text-align: left;
  }
  .exp {
    width: 100%;
    font-size: 1rem;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 1%;
    .period {
      width: 8vw;
      min-width: 230px;
      color: gray;
      font-size: 1.3rem;
      font-style: italic;
    }
    .content {
      width: 35vw;
      min-width: 300px;
      text-align: left;
      .project-name {
        font-size: 1.4rem;
        font-weight: bold;
      }
      .project-client {
        font-style: italic;
        color: gray;
        margin-bottom: 3%;
      }

      .li {
        margin-bottom: 2%;
        .sub-command {
          display: flex;
          margin-left: 25px;
          margin-top: 5px;
          font-size: 0.9rem;
          .why-button {
            color: #1d9bf0;
            width: fit-content;
            cursor: pointer;
            min-width: 62px;
          }
          .why-button:hover {
            text-decoration : underline;
          }
          .li {
            margin-left: 8px;
            list-style: none;
          }
        }
      }

      .table {
        font-size: 1rem;
        text-align: center;
        width: 100%;
        margin-top: 4%;
        .td {
          border: solid 1px gray;
          padding: 2%;
        }
      }
    }
  }
}

.education {
  width: 100%;
  height: fit-content;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  .header {
    width: 47%;
    min-width: 230px;
    font-size: 1.7rem;
    /* color: rgb(96, 174, 248); */
    font-weight: bold;
    text-align: left;
  }
  .exp {
    width: 100%;
    font-size: 1rem;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 1%;
    .period {
      width: 8vw;
      min-width: 230px;
      color: gray;
      font-size: 1.3rem;
      font-style: italic;
    }
    .content {
      width: 35vw;
      min-width: 300px;
      text-align: left;
      .title {
        font-size: 1.4rem;
        font-weight: bold;
      }
      .sub-content {
        font-style: italic;
        color: gray;
        margin-bottom: 3%;
      }
    }
  }
}

.etc {
  width: 100%;
  height: fit-content;
  display: block;
  /* justify-content: center;
  flex-wrap: wrap; */

  .header {
    width: 47%;
    min-width: 300px;
    margin: auto;
    font-size: 1.7rem;
    /* color: rgb(96, 174, 248); */
    font-weight: bold;
    text-align: left;
  }
  .content {
    width: 47vw;
    min-width: 300px;
    max-width: 100vw;
    margin: auto;
    font-size: 1rem;
    word-break: normal;
    text-align: left;
    .ul {
      margin-bottom: 2%;
      .li {
        word-break: normal;
        .preview-txt {
          color: #1d9bf0;
          cursor: pointer;
        }
        .preview-txt:hover {
          text-decoration : underline;
        }
      }
    }
  }
}

.stag {
  border-radius: 5px;
  background-color: rgb(190, 190, 190);
  color: black;
  width: fit-content;
  font-size: 0.8rem;
  font-weight: bold;
  margin-left: 1%;
  padding-left: 1%;
  padding-right: 1%;
}

@media (max-width: 1111px) {
  .rightLine {
    right: 10px;
    .ul-wrapper {
      .label {
        display: none;
      }
    }
  }
  .modal {
    .wrapper {
      width: 100% !important;
      height: 70% !important;
    }
  }
}

@media (max-width: 960px) {
  .skill {
    .table {
      overflow-x: auto;
    }
  }
  .modal {
    .wrapper {
      height: 60% !important;
      .imageWrapper:hover {
        border: 1px solid #125e919f !important;
      }
      .selected {
        border: 1px solid #1d9bf0 !important;
        &:hover {
          border: 1px solid #1d9bf0 !important;
        }
      }
    }
  }
}

@media (max-width: 600px) {
  .main-test {
    padding: 0;
  }
  .rightLine {
    right: 0;
  }
  .h-line {
    margin-top: 7%;
    margin-bottom: 10%;
    width: 73%;
  }
  .profile-info {
    flex-direction: column;
    align-items: center;
    .my-image {
      margin-bottom: 2%;
    }
    .description {
      width: 80%;
    }
  }
  .topLine {
    width: 90%;
  }
  .header {
    width: 73% !important;
    margin-bottom: 20px;
  }
  .work-exp {
    .exp {
      .period {
        text-align: left;
        width: 73%;
        margin-bottom: 5%;
        .box {
          margin-left: 0;
        }
      }
    }
  }
  .project {
    .exp {
      .period {
        text-align: left;
        width: 73%;
        margin-bottom: 5%;
      }
    }
  }
  .education {
    .exp {
      .period {
        text-align: left;
        width: 73%;
      }
      .content {
        width: 73%;
      }
    }
  }
  .etc {
    .content {
      width: 80%;
    }
  }
  .modal {
    .wrapper {
      height: 50% !important;
      .arrowBtn {
        min-width: 60px !important;
        width: 60px !important;
      }
      .listView {
        gap: 2px !important;
      }
    }
  }
}